button {
  padding: 0, 10px, 50px, 10px;
  margin-left: 40%;
  height: 50px;
  width: 20%;
  border-width: medium;
  border-color: gray;
  font-weight: bold;
  border-radius: 5px;
  font-size: 20px;
}

.form-group {
  margin-left: 33%;
  /* margin-top: 50px; */
}

label {
  font-weight: bold;
  margin-top: 10px;
  font-size: 30px;
  margin-right: 1vw;
}

input {
  height: 35px;
  border-radius: 5px;
  display: inline-block;
  margin-top: -10px;
  width: 30%
}

@media (max-width: 375px) {

  label {
    margin-left: 20%;
  }
  button {
    width: 60%;
    margin-left: 20%;
  }

  .form-group {
    margin-left: 15%;
    /* margin-top: 50px; */
  }

  input {
    width: 60%;
    margin-left: 14%;
  }
}

@media (max-width: 414px) {
  button {
    width: 60%;
    margin-left: 20%;
  }

  .form-group {
    margin-left: 15%;
    /* margin-top: 50px; */
  }

  input {
    width: 60%;
  }
}


